@use '../../00-base/init' as *;

/* Tab Navigation */
.m-tabs__nav {
  padding: 0;
  border-bottom: 1px solid var(--ctek-global-color-gray-500);

  @include bp(lg) {
    display: flex;
    border-left: 1px solid var(--ctek-global-color-gray-500);
  }
}

/* Tab Content (hidden only in full #tabs js version) */
.m-tabs__tab {
  display: none;

  &.is-active {
    display: block;
  }
}

.m-tabs__content {
  padding: 1.5rem;
}

/* No-js fallback */
.m-tabs.no-js {

  .tabs__tab.is-active {
    display: block;
  }
}

/* Drupal Local Tasks variant */
.m-tabs__nav--local-tasks {
  margin-top: 1em;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 1em;

  padding: 0;
}

.m-tabs__link--local-tasks {
  padding: 0.3em 1.5em;
}
