/**
 * Global Breakpoints
 *
 * ref: https://rimdev.io/making-media-query-mixins-with-sass/
 *
 * This system has the following features
 * 1. Can create a map of breakpoint variables using any naming scheme
 * 2. Media direction can be specified; default direction is min-width
 * 3. Math is used on max-width directions to subtract 1px from the variable value to prevent breakpoint conflicts
 * 4. Adhoc values can be used and will be passed through directly, no math is done when using max-width.
 *
 * EXAMPLES
 *
 * To create a default min-width mq:
 * @include bp(md) {
 *   color: black;
 * }
 *
 * OUTPUTS:
 * @media (min-width: 992px) {
 *   .link {
 *     color: #000;
 *   }
 * }
 *
 * To create a max-width mq:
 * @include bp(sm, max) {
 *   color: yellow;
 * }
 *
 * OUTPUTS:
 * @media (max-width: 767px) {
 *   .link {
 *     color: #ff0;
 *   }
 * }
 *
 * To create a min-width and max-width mq:
 * @include bp(xs) {
 *   @include bp(md, max) {
 *     color: #bada55;
 *   }
 * }
 *
 * OUTPUTS:
 * @media (min-width: 375px) and (max-width: 991px) {
 *   .link {
 *     color: #bada55;
 *   }
 * }
 *
 * To create an ad-hoc min-width and max-width mq:
 * @include bp(300px) {
 *   @include bp(600px, max) {
 *     color: gray;
 *   }
 * }
 *
 * OUTPUTS:
 * @media (min-width: 300px) and (max-width: 600px) {
 *   .link {
 *     color: gray;
 *   }
 * }
*/

@use 'sass:map';

$breakpoints: (
  xs: 375px,
  sm: 768px,
  md: 992px,
  lg: 1200px,
  xl: 1400px,
  xxl: 1600px,
);

@mixin bp($breakpoint, $direction: min) {

  @if map-has-key($breakpoints, $breakpoint) {
    // Get the breakpoint value.
    $breakpoint-value: map.get($breakpoints, $breakpoint);

    @if $direction == max {

      @media screen and (max-width: ($breakpoint-value - 1)) {
        @content;
      }
    } @else if $direction == min {

      @media screen and (min-width: $breakpoint-value) {
        @content;
      }
    } @else {

      @media screen and ($direction: $breakpoint-value) {
        @content;
      }
    }

    // If the breakpoint doesn't exist in the map.
  } @else {

    @if $direction == max {

      @media screen and (max-width: $breakpoint) {
        @content;
      }
    } @else if $direction == min {

      @media screen and (min-width: $breakpoint) {
        @content;
      }
    } @else {

      @media screen and ($direction: $breakpoint) {
        @content;
      }
    }
  }
}
